<template>
	<div>
		<span class="dance-floor"></span>
		<span class="reception">Reception</span>
		<span class="entrance">▼ 入口 ▲</span>
		<span v-for="item in items" ref="areas"
				:key="item.key"
				:style="computeAreaStyle(item)"
				:class="computeClassName(item)"
				@click.stop="emitSelect(item)">{{ item.key }}</span>
	</div>
</template>

<script>
export default {
	name: 'Map',
	props: {
		items: { type: Array },
		focusedItem: { type: Object }
	},
	methods: {
		emitSelect(item) {
			this.$emit('select', item);
		},
		computeAreaStyle(item) {
			let table = item.value;
			let scale = table.scale || 1;
			let size = table.size || 30;
			if (!Array.isArray(size)) size = [size, size];
			let offsetX = size[0] >> 1;
			let offsetY = size[1] >> 1;
			return {
				left: (table.position[0] || 0) - offsetX + 'px',
				top: (table.position[1] || 0) - offsetY + 'px',
				height: `${size[1]}px`,
				width: `${size[0]}px`,
				'border-radius': `${table.radius || 0}px`,
				transform: `scale(${scale})`
			}
		},
		computeClassName(item) {
			return {
				table: true,
				focused: this.focusedItem && this.focusedItem.key === item.key
			}
		}
	}
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

div {
	background-color: red;
	font-size: 40px;
	width: 700px;
	height: 1000px;
	background-color: #A8B8BF;
	border-radius: 5px;
	overflow: hidden;
}

span.dance-floor {
	display: block;
	background-color: #5C758E;
	height: 260px;
}

span.reception {
	position: absolute;
	left: 220px;
	top: 700px;
	background-color: #409CD2;
	width: 200px;
	height: 80px;
	border-radius: 10px;
	display: flex;
	justify-content: center;
	align-items: center;
	font-size: 30px;
	transform: rotate(90deg);
	box-shadow: 3px 3px 10px 0px rgba(0, 0, 0, 0.5);
}

span.entrance {
	position: absolute;
	right: 50px;
	bottom: 0px;
	color: #1B2838;
	position: absolute;
	background-color: #FFFFFF88;
	border-radius: 15px 15px 0 0;
	font-size: 40px;
	padding: 10px 15px 5px;
}

span.table {
	position: absolute;
	background-color: #1B2838;
	display: flex;
	justify-content: center;
	align-items: center;
	transition: all 0.2s ease-out;
	cursor: pointer;
	color: #FFF;
	box-shadow: -3px 3px 10px 0px rgba(0, 0, 0, 0.5);
}

span.table.focused {
	background-color: #FFF;
	color: #1B2838;
	box-shadow: -10px 10px 30px 10px rgba(0, 0, 0, 0.5);
	font-weight: bold;
	z-index: 100;
	transform: scale(1.3)!important;
}

</style>



